h3{
    margin: 0;
    padding: 0;
}
.topMain main .weatherContainer{
    font-size: 22px;
    color: #d9d9d9;
    margin-top: 35px;
}

.topMain main .weatherContainer button{
    color: #031B52;
    background: #FFB95D;
    outline: none;
    border: none;
    width: 210px;
    height: 32px;
    font-size: 16px;
    border-radius: 10px;
    font-weight: bold;
}
.topMain main .weatherContainer .weatherInfo{
    width: 25%;
    display: grid;
    grid-template-columns: 65% 35%;
    grid-gap: 10px;
    align-items: center;
    text-align: center; 
}
.topMain main .weatherContainer .weatherInfo img{
    width: 100%;
}

.topContainer .topMain main .weatherContainer .popUp{
    position: absolute;
    height: 65%;
    width: 30%;
    top: 0;
    left: 35%;
    background: rgba(255,255,255,0.3);
    overflow-y: scroll;
    -webkit-animation: animate .5s;
            animation: animate .5s;
    display: none;
}
.topContainer .topMain main .weatherContainer .popUp button{
    background: #cc4242;
    color: white;
    height: 25px;
    width: 25px;
    text-align: center;
    position: absolute;
    right: 0;
    top: 0;
}

.topContainer .topMain main .weatherContainer .popUp select{
    width: 90%;
    height: 30px;
    outline: none;
    font-size: 16px;
    padding: 5px;
    font-family: "Rockwell";
    background: #E1E1E1;
}
.topContainer .topMain main .weatherContainer .popUp select option{
    margin: 15px;
    font-size: 18px;
    height: 30px;
    background: transparent;

}


@-webkit-keyframes animate{
    from{
        opacity: 0
    }
    to{
        opacity: 1;
    }
}


@keyframes animate{
    from{
        opacity: 0
    }
    to{
        opacity: 1;
    }
}
::-webkit-scrollbar{
    background: transparent
}
.bottomMain {
    display: grid;
    grid-template-columns: repeat(7 ,1fr);
    grid-gap: 20px;
}
.bottomMain .week{
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: 70% 30%;
    align-items: center;
    justify-items: center;
    text-align: center;
    color: #BCBCBC;
    font-size: 18px;
    font-family: "Rockwell";
    background: rgba( 0,0,0,0.3)
}
.bottomMain .week div{
    width: 100%;
}
.bottomMain .week img{
    width: 70%;
}

*{
    box-sizing: border-box;
}
html , body ,#root{
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    font-family: "Rockwell";
}

.App{
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-rows: 65% 35%;
}

.App .topContainer{
    width: 100%;
    height: 100%;
    background: url("/weatherApp/static/media/Group 10.4f85b765.png") no-repeat;
    background-size: cover;
    overflow: hidden;
}

.App .topContainer .topMain{
    width: 80%;
    height: 90%;
    margin: 25px auto;
    overflow: hidden;

}
.App .topContainer .topMain header{
    font-size: 32px;
    color: #eeeeee;
    font-weight: bold;
}

.App .bottomContainer{
    width: 100%;
    height: 100%;
    background: url("/weatherApp/static/media/Group 11.121300a0.png") no-repeat;
    background-size: cover;
    overflow: hidden;
    color: #eeeeee;

}
.App .bottomContainer h3{
    margin: 1% 0 0 10%;
    font-size: 24px;
    padding: 0;
}
.App .bottomContainer .bottomMain{
    width: 80%;
    height: 65%;
    margin: 15px auto;
}

