
*{
    box-sizing: border-box;
}
html , body ,#root{
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    font-family: "Rockwell";
}

.App{
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-rows: 65% 35%;
}

.App .topContainer{
    width: 100%;
    height: 100%;
    background: url("./Image/Group\ 10.png") no-repeat;
    background-size: cover;
    overflow: hidden;
}

.App .topContainer .topMain{
    width: 80%;
    height: 90%;
    margin: 25px auto;
    overflow: hidden;

}
.App .topContainer .topMain header{
    font-size: 32px;
    color: #eeeeee;
    font-weight: bold;
}

.App .bottomContainer{
    width: 100%;
    height: 100%;
    background: url("./Image/Group\ 11.png") no-repeat;
    background-size: cover;
    overflow: hidden;
    color: #eeeeee;

}
.App .bottomContainer h3{
    margin: 1% 0 0 10%;
    font-size: 24px;
    padding: 0;
}
.App .bottomContainer .bottomMain{
    width: 80%;
    height: 65%;
    margin: 15px auto;
}
